.card{
   width: 230px;
   display: flex;
   flex-direction: column;
   gap:10px;
   margin: 20px 0px;

   

    .image{
        width: 100%;
        height: 300px;
        overflow: hidden;
        position: relative;
        border-radius: 10px;
        
        
        

      &:hover{
        .secondImg{
            z-index: 2;
        }
      }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            
        }

        .mainImg{
            z-index: 1;
        }

        span{
            position: absolute;
            top: 5px;
            left: 5px;
            background-color: white;
            color: teal;
            padding: 3px 5px;
            z-index: 3;
            font-weight: 500;
            font-size: 12px;

        }


    }
    h2{
        font-size: 16px;
        font-weight: 400;
    }
    .prices{
        display: flex;
        gap: 20px;


        h3{
            font-size: 18px;
            font-weight: 500;
    
    
            &:first-child{
                color: gray;
                text-decoration: line-through;
            }
        }
    }
    

    
}
